@import url("https://fonts.googleapis.com/css?family=Assistant:200,300,400,600,700,800|Lora:400,400i,700,700i");
@import url('https://fonts.googleapis.com/css?family=Ubuntu+Mono&display=swap');

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Assistant", sans-serif;
}

body.light-mode {
  background-color: #FAFCFF;
  color: #333;
  transition: background-color 0.3s ease;
}

body.dark-mode {
  background-color: #33374d;
  color: #dfdfdf;

  .name {
    color: #7e57c2;
  }

  .headline,
  .section-body {
    color: #d0d0d0;
  }

  .section-title {
    color: #b199db;
    background-color: #282C34;
  }

  .profile-button {
    color: #58e76f;
  }

  .blockquote-bio {
    border-left: 0.1em solid #7e57c2;
    margin-left: 0;
    padding-left: 1em;
  }
  a:link,
  a:visited,
  a:active {
    color: #58e76f;
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }

  .dark-mode-toggle > button {
    color: #b199db;
    &:last-child {
      color: #b199db;
    }
  }
}

.toggle-button-wrapper {
  top: 0;
  margin-bottom: 2em;
}

.content {
  padding: 10%;
  margin: 0 auto;
}

.dark-mode-toggle {
  display: flex;
  margin: 0 auto;
  & > button {
    font-size: 1.2em;
    background: none;
    border: none;
    color: #7e57c2;
    cursor: pointer;
    transition: color 0.3s ease;
    &:last-child {
      color: #7e57c2;
    }

    &:focus {
      outline: none;
    }
  }
}

.toggle-control {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type="checkbox"].dmcheck {
  width: 50px;
  height: 15px;
  background: #58e76f;
  position: relative;
  border-radius: 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;

  &:checked + label {
    left: 40px;
  }

  &:focus-visible {
    outline: solid 2px #58e76f;
  }

  & + label {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: absolute;
    left: 2px;
    background: #fff;
    opacity: 0.9;
    background-color: #ffffff;
  }
}

.header {
  top: 0;
}

.name {
  font-size: 2em;
  font-family: "Lora", serif;
  font-style: italic;
  letter-spacing: -1px;
  color: #334256;
}

.headline {
  font-size: 1.2em;
  color: #90A3BD;
}

.section {
  margin-top: 5em;
  width: 100%;
}

.section-title {
  font-size: 1em;
  font-family: 'Ubuntu Mono', monospace;
  color: rgb(59, 59, 59);
  background-color: #F0F6FF;
  border-radius: 0.5em;
  padding: 0.4em;
}

.section-body {
  color: #90A3BD;
}

.profile-button {
  padding: 0.5em;
  margin-top: 2em;
  margin-right: 1em;
  width: 5em;
  text-align: center;
  display: inline-block;
  border-radius: 3em;
  border: 2px solid;
  font-weight: 600;
  transition: 0.2s ease-out;
  color: #7e57c2;
}

.profile-button:hover {
  opacity: 1;
  transition: 0.2s ease-in;
}

.blockquote-bio {
  border-left: 0.1em solid rgb(85, 85, 85);
  margin-left: 0;
  padding-left: 1em;
}

.designs {
  width: 100%;
  margin-top: 3em;
}

.design-title {
  font-size: 1em;
  font-family: "Lora", serif;
  font-style: italic;
}

.design-img {
  width: 100%;
  margin-bottom: 4em;
  transition: 0.2s ease-out;
}

.design-img:hover {
  opacity: 0.5;
  transition: 0.2s ease-in;
}

a:link,
a:visited,
a:active {
  color: rgb(114, 127, 146);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* For tablets: */
@media only screen and (min-width: 601px) {
  .col-s-1 {
    width: 8.33%;
  }
  .col-s-2 {
    width: 16.66%;
  }
  .col-s-3 {
    width: 25%;
  }
  .col-s-4 {
    width: 33.33%;
  }
  .col-s-5 {
    width: 41.66%;
  }
  .col-s-6 {
    width: 50%;
  }
  .col-s-7 {
    width: 58.33%;
  }
  .col-s-8 {
    width: 66.66%;
  }
  .col-s-9 {
    width: 75%;
  }
  .col-s-10 {
    width: 83.33%;
  }
  .col-s-11 {
    width: 91.66%;
  }
  .col-s-12 {
    width: 100%;
  }
}

/* For desktop: */
@media only screen and (min-width: 800px) {
  .col-1 {
    width: 8.33%;
  }
  .col-2 {
    width: 16.66%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4 {
    width: 33.33%;
  }
  .col-5 {
    width: 41.66%;
  }
  .col-6 {
    width: 50%;
  }
  .col-7 {
    width: 58.33%;
  }
  .col-8 {
    width: 66.66%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.33%;
  }
  .col-11 {
    width: 91.66%;
  }
  .col-12 {
    width: 100%;
  }

  .section {
    margin-top: 8em;
    width: 60%;
  }

  .designs {
    width: 60%;
    margin-top: 3em;
  }

  .name {
    font-size: 3em;
    font-family: "Lora", serif;
    font-style: italic;
    letter-spacing: -1px;
    color: #334256;
  }
}
